.ant-col{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;
}
.headerElement{
    margin-bottom: 2rem;
}
.pagination{
    margin: auto;
}
@media not screen and (orientation: landscape){
    /* #root{
        display: none;
    } */
    .buttonRight{

    }
    .header-field{
        display: flex;
        justify-content: center;
        flex-direction: column;
    }
    .ant-layout-header{
        width: 100%;
        padding: 0;
    }
    .ant-row-flex-space-between{
        width: 90vw;
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        
    }
    .ant-typography>a{
        font-size: 2rem;
    }
    .ant-layout-footer{
        padding: 0;
    }
    .ant-pagination{
        width: 80vw;
        display: inline;
    }
    .searchField{
        width: 70vw;
    }
}